<template>
  <div>
    <header>
      <g-header></g-header>
    </header>
    <h4>{{goodsDescInfo.title}}</h4>
    <hr />
    <div class="goodsDesc" v-html="goodsDescInfo.content"></div>
    <div id="tabbar">
      <g-tabbar></g-tabbar>
    </div>
  </div>
</template>
<script>
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
export default {
  data () {
    return {
      goodsDescInfo: {}
    }
  },
  created () {
    this.getGoodsDesc()
  },
  methods: {
    async getGoodsDesc () {
      const res = await this.$http.get(
        '/api/goods/getdesc/' + this.$route.params.id
      )
      if (res.status !== 0) {
        return this.$toast('请求服务器响应失败')
      }
      console.log(res.message[0])
      this.goodsDescInfo = res.message[0]
    }
  },
  components: {
    GHeader,
    GTabbar
  }
}
</script>
<style lang="less" >
#app {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}
header {
  width: 100%;
}
h4 {
  font-size: 16px;
  color: #226aff;
  text-align: center;
  margin: 15px 0;
}
.goodsDesc {
  width: 100%;
  padding: 0 5px;
}
.gomeImgLoad {
  width: 100% !important;
}
</style>
