<template>
  <van-tabbar v-model="active" route>
    <van-tabbar-item to="/home">
      <span>首页</span>
      <template #icon="props">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="props.active?'#icon-house-add':'#icon-house'" />
        </svg>
      </template>
    </van-tabbar-item>
    <van-tabbar-item icon="qr" to="/classify">
      <span>分类</span>
      <template #icon="props">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="props.active?'#icon-fenlei1':'#icon-fenlei'" />
        </svg>
      </template>
    </van-tabbar-item>
    <van-tabbar-item icon="contact" to="/member">
      <span>会员</span>
      <template #icon="props">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="props.active?'#icon-huiyuanjulebu':'#icon-renzhenghuiyuan'" />
        </svg>
      </template>
    </van-tabbar-item>
    <van-tabbar-item icon="cart-o" :badge="cartData.length" to="/cart">
      <span>购物车</span>
      <template #icon="props">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="props.active?'#icon-ziyuan':'#icon-gouwudai'" />
        </svg>
      </template>
    </van-tabbar-item>
    <van-tabbar-item icon="search" to="/search">
      <span>搜索</span>
      <template #icon="props">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="props.active?'#icon-sousuo':'#icon-sousuo1'" />
        </svg>
      </template>
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'GTabbar',
  data () {
    return {
      active: 0
    }
  },
  computed: {
    ...mapState(['cartData'])
  },
  created () {
    this.getCartData()
  },
  methods: {
    ...mapMutations(['addToCart']),
    getCartData () {
      if (this.cartData.length > 0 || !localStorage.getItem('cartNum')) {

      } else {
        const dataShow = JSON.parse(localStorage.getItem('cartData'))
        dataShow.forEach(item => this.addToCart(item))
      }
    }
  }
}
</script>

<style lang="less" scoped>
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>
