<template>
  <div id="app">
    <g-header></g-header>
    <div style="padding:10px">
      <!-- 轮播图区域 -->
      <div class="swiper">
        <swiper :swiperInfo="swiperImg"></swiper>
      </div>
      <!-- 商品信息区域 -->
      <div class="goodsInfo">
        <div class="goodsInfo-title">{{goodsDetailsInfo.title}}</div>
        <div class="goodsPrice">
          市场价:
          <span>{{goodsDetailsInfo.market_price}}</span>
          销售价:
          <em>{{goodsDetailsInfo.sell_price}}</em>
        </div>
        <div class="count">
          <span>购买数量</span>
          <van-stepper
            v-model="value"
            integer
            input-width="40px"
            button-size="32px"
            :max="goodsDetailsInfo.stock_quantity"
          />
        </div>
        <div class="btns">
          <van-button type="info" size="small">立即购买</van-button>
          <van-button
            color="#f44"
            size="small"
            @click="addToCart({id:$route.params.id,num: value})"
          >加入购物车</van-button>
        </div>
      </div>
      <!-- 商品参数区域 -->
      <div class="goodsParams">
        <div class="title">商品参数</div>
        <div class="body">
          <p>商品货号:{{goodsDetailsInfo.goods_no}}</p>
          <p>库存情况:{{goodsDetailsInfo.stock_quantity}}件</p>
          <p>上架时间:{{goodsDetailsInfo.add_time | dateFormat()}}</p>
        </div>
        <div class="btns">
          <van-button
            plain
            type="info"
            block
            @click="$router.push('/goodsdesc/'+$route.params.id)"
          >图文介绍</van-button>
          <van-button
            plain
            type="danger"
            block
            @click="$router.push('/goodscomments/'+$route.params.id)"
          >商品评论</van-button>
        </div>
      </div>
    </div>
    <div id="tabbar">
      <g-tabbar></g-tabbar>
    </div>
  </div>
</template>
<script>
import swiper from '../swiper/swiper'
import { mapMutations } from 'vuex'
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
export default {
  data () {
    return {
      active: 0,
      // 轮播图数组
      swiperImg: [],
      // 详情信息数据对象
      goodsDetailsInfo: {},
      // 步进器数值
      value: 1
    }
  },

  created () {
    this.getGoodsDetailsImg()
    this.getGoodsDetails()
  },
  methods: {
    ...mapMutations(['addToCart']),
    //   轮播图图片调用接口
    async getGoodsDetailsImg () {
      const res = await this.$http.get(
        '/api/getthumimages/' + this.$route.params.id
      )
      if (res.status !== 0) {
        return this.$notify.danger('获取图片失败')
      }
      // console.log(res);
      res.message.forEach(item => {
        this.swiperImg.push(item.src)
        // console.log(item.src);
      })
      // console.log(this.swiperImg);
    },
    // 获取商品详情数据
    async getGoodsDetails () {
      const res = await this.$http.get(
        '/api/goods/getinfo/' + this.$route.params.id
      )
      if (res.status !== 0) {
        return this.$toast('获取详情失败')
      }
      // console.log(res);
      this.goodsDetailsInfo = res.message[0]
      // console.log(this.goodsDetailsInfo);
    }
  },
  components: {
    swiper: swiper,
    GHeader,
    GTabbar
  }
}
</script>
<style lang="less" scoped>
.goodsParams {
  margin-top: 10px;
  padding: 15px;
  border-radius: 3px;
  border: 1px solid #ccc;
}
.goodsInfo {
  margin-top: 10px;
  padding: 15px;
  border-radius: 3px;
  border: 1px solid #ccc;
}
.goodsInfo-title {
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}
.goodsPrice {
  font-size: 14px;
  margin: 10px;
  color: #8f8f94;
}
.goodsPrice span {
  text-decoration-line: line-through;
  margin: 0 10px;
}
.goodsPrice em {
  color: red;
  font-weight: 700;
  font-size: 16px;
}
.count {
  padding: 10px;
}
.count span {
  font-size: 14px;
  color: #8f8f94;
}
.van-stepper {
  display: inline-block;
  margin-left: 15px;
  vertical-align: middle;
}
.btns .van-button {
  margin-right: 5px;
  padding: 0 15px;
}
.title {
  border-bottom: 1px solid #ccc;
  font-size: 17px;
  padding-bottom: 10px;
}
.goodsParams .body {
  padding: 15px;
  border-bottom: 1px solid #ccc;
}
.goodsParams p {
  font-size: 14px;
  margin: 10px 0;
  color: #8f8f94;
}
.goodsParams .btns {
  margin-top: 15px;
}
.goodsParams .btns .van-button {
  height: 39px;
  line-height: 39px;
  border-radius: 6px;
  margin: 5px 0;
}
</style>
