<template>
  <div>
    <header>
      <g-header></g-header>
    </header>
    <van-sticky :offset-top="50">
      <!-- 筛选区域 -->
      <div class="filter_top">
        <van-dropdown-menu active-color="#ee0a24" :overlay="overlay">
          <van-dropdown-item v-model="value1" :options="option1" @open="overlay=true" />
          <van-dropdown-item title="销量" @open="overlay=false"></van-dropdown-item>
          <van-dropdown-item v-model="value2" :options="option2" @open="overlay=true" />
          <van-dropdown-item title="筛选" ref="filterRef" @open="overlay=true">
          </van-dropdown-item>
        </van-dropdown-menu>
      </div>
      <!-- 副筛选模块 -->
      <div class="filter_bottom">
        <van-dropdown-menu :overlay="overlay">
          <van-dropdown-item title="新品" @open="overlay=false" />
          <van-dropdown-item title="品牌" ref="brandRef" @open="overlay=true">
            <van-checkbox-group v-model="brandResult" ref="brandCheckboxGroupRef">
              <van-cell-group>
                <van-cell
                  v-for="(item, index) in brandList"
                  clickable
                  :key="index"
                  :title="item"
                  @click="brandToggle(index)"
                >
                  <template #right-icon>
                    <van-checkbox :name="item" ref="brandCheckBoxRef"
                    v-model="checked"
                    />
                  </template>
                </van-cell>
              </van-cell-group>
            </van-checkbox-group>
            <div class="btns">
              <van-button type="default" @click="resetAll('brandCheckBoxRef')">重置</van-button>
              <van-button type="danger" @click="confirm('brandRef')">确认</van-button>
            </div>
          </van-dropdown-item>
          <van-dropdown-item title="操作系统" ref="operatingRef" @open="overlay=true">
            <van-checkbox-group v-model="operatingResult"  ref="opreatingCheckboxGroupRef">
              <van-cell-group>
                <van-cell
                  v-for="(item, index) in operatingList"
                  clickable
                  :key="index"
                  :title="item"
                  @click="operatingToggle(index)"
                >
                  <template #right-icon>
                    <van-checkbox :name="item" ref="operatingCheckBoxRef" />
                  </template>
                </van-cell>
              </van-cell-group>
            </van-checkbox-group>
            <div class="btns">
              <van-button type="default" @click="resetAll('operatingCheckBoxRef')">重置</van-button>
              <van-button type="danger" @click="confirm('operatingRef')">确认</van-button>
            </div>
          </van-dropdown-item>
          <van-dropdown-item title="拍拍二手" @open="overlay=false" />
        </van-dropdown-menu>
      </div>
    </van-sticky>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <van-cell
          v-for="item in goodslist"
          :key="item.id"
          v-lazy="item.img_url"
          @click="goDetail(item.id)"
        >
          <van-image :src="item.img_url"></van-image>
          <p>{{item.title}}</p>
          <div class="bottom_box">
            <div>
              惊爆价:
              <span>{{item.sell_price}}</span>
              <s>原价:{{item.market_price}}</s>
            </div>
            <div>
              <span>热卖中</span>
              <span>剩余{{item.stock_quantity}}件</span>
            </div>
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
    <div id="tabbar">
      <g-tabbar></g-tabbar>
    </div>
  </div>
</template>
<script>
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
export default {
  data () {
    return {
      goodslist: [],
      loading: false,
      finished: false,
      isLoading: false,
      pageindex: 0,
      overlay: true,
      checked: true,
      // 下拉菜单
      value1: 0,
      value2: 'a',
      // 综合下拉框
      option1: [
        { text: '综合', value: 0 },
        { text: '最新上架', value: 1 },
        { text: '价格最低', value: 2 },
        { text: '价格最高', value: 3 },
        { text: '评论最多', value: 4 }
      ],
      // 配送下拉框
      option2: [
        { text: '有货优先', value: 'a' },
        { text: '货到付款', value: 'b' },
        { text: 'PLUS专享', value: 'c' },
        { text: '促销商品', value: 'd' }
      ],
      brandResult: [],
      // 品牌列表
      brandList: [
        '小米',
        '魅族',
        'Apple',
        '华为(HUAWEI)',
        '三星(Samsung)',
        '一加',
        'OPPO',
        'vivo',
        '黑鲨',
        'realme',
        '诺基亚(Nokia)',
        '努比亚(nubia)',
        'IQOO',
        '小辣椒',
        '中兴(ZTE)',
        '锤子(smartisan)'
      ],
      brandCheckboxGroup: [],
      operatingResult: [],
      // 操作系统列表
      operatingList: ['功能机', 'Android(安卓)', '其他OS', 'IOS(Apple)'],
      kuaidiList: [
        '顺丰物流',
        '有货优先',
        '货到付款',
        '新品',
        '大牌国际',
        'PLUS专区',
        '促销商品',
        '配送全球',
        '拍拍二手',
        '百亿补贴'
      ],
      categories: [
        '热门推荐',
        '手机数码',
        '家用电器',
        '电脑办公',
        '计生情趣',
        '美妆护肤',
        '个护清洁',
        '汽车生活',
        '京东超市',
        '男装',
        '男鞋',
        '女装',
        '女鞋',
        '母婴童装',
        '图书音像',
        '运动户外',
        '内衣配饰',
        '食品生鲜',
        '酒水饮料',
        '家具家装',
        '家居厨具',
        '箱包手袋',
        '钟表珠宝',
        '玩具乐器',
        '医药保健',
        '宠物生活',
        '礼品鲜花',
        '农资绿植',
        '生活旅行',
        '奢侈品',
        '京东国际',
        '艺术邮币',
        '二手商品',
        '特产馆',
        '京东金融',
        '国际名牌',
        '拍卖',
        '房产',
        '工业品']
    }
  },

  methods: {
    onLoad () {
      // 异步更新数据
      console.log(123)
      this.pageindex++
      this.getList()
    },
    async getList (cb) {
      const res = await this.$http.get(
        '/api/getgoods?pageindex=' + this.pageindex
      )
      if (res.status === 1) {
        return (this.loading = true)
      }
      if (res.message < 10) {
        this.loading = false
        this.finished = true
        return
      }
      if (res.status !== 0) {
        this.finished = true
        return this.$toast('获取商品失败')
      }
      cb && cb()
      res.message.forEach(item => this.goodslist.push(item))
      this.loading = false
      // console.log(res.message);
    },
    onRefresh () {
      this.pageindex = 1
      this.goodslist = []
      this.getList(() => {
        setTimeout(_ => {
          this.isLoading = false
        }, 800)
      })

      this.finished = false
    },
    goDetail (id) {
      this.$router.push('/goods/detail/' + id)
    },
    // 筛选复选框事件
    brandToggle (index) {
      this.$refs.brandCheckBoxRef[index].toggle()
    },
    operatingToggle (index) {
      this.$refs.operatingCheckBoxRef[index].toggle()
    },
    // 重置复选框
    resetAll (ref) {
      // this.$refs.brandCheckBoxRef.forEach(item=>{
      //   item.checked=false
      // })
      if (ref === 'brandCheckBoxRef') {
        this.$refs.brandCheckboxGroupRef.toggleAll(false)
      } else {
        this.$refs.opreatingCheckboxGroupRef.toggleAll(false)
      }
    },
    // 点击确认按钮事件
    confirm (ref) {
      if (ref === 'brandRef') {
        this.$refs.brandRef.toggle()
      } else {
        this.$refs.operatingRef.toggle()
      }
    }
  },
  created () {
    this.getList()
  },
  components: {
    GHeader,
    GTabbar
  }
}
</script>
<style lang="less" scoped>
header {
  height: 45px !important;
}
.van-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 5px 10px;
  .van-cell {
    width: 48%;
    border: 1px solid #ccc;
    margin: 10px 0px;
    padding: 0px;
    align-content: space-between;
    .van-cell__value--alone {
      // justify-content: center;
      text-align: center;
      .van-image {
        padding: 10px;
        min-height: 180px;
      }
    }
    img {
      min-height: 180px;
      height: 180px;
      margin: auto;
    }
    .van-cell__value {
      > p {
        font-size: 14px;
        color: #000;
        text-align: left;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
        padding: 4px 10px;
        height: 49px;
      }
    }
    .bottom_box {
      width: 100%;
      background: #eee;
      padding: 5px 0px;
      div:nth-child(1) {
        text-align: left;
        padding: 0px 10px;
        span:nth-child(1) {
          font-size: 16px;
          color: red;
          margin-right: 15px;
        }
      }
      div:nth-child(2) {
        display: flex;
        justify-content: space-between;
        padding: 0px 10px;
      }
    }
  }
}
.filter_top {
  /deep/.van-cell {
    margin-top: 5px;
  }
}
.filter_bottom {
  /deep/.van-dropdown-menu__item {
    height: 30px;
    background-color: #f2f2f7;
    margin: 0 5px;
    box-sizing: border-box;
    span {
      font-size: 10px;
    }
  }
  .van-cell--clickable {
    display: inline-block;
    width: 185px;
    box-sizing: border-box;
    .van-cell__title {
      float: left;
    }
    .van-checkbox {
      float: right;
    }
  }
  .btns {
    display: flex;
    height: 50px;
    .van-button {
      flex: 1;
    }
  }
}
</style>
